<template>
    <div class="comments_list">
        <div class="comment_element"
             v-for="(comment, index) in comments"
             :key="index">
            <div class="author">
                {{ comment.email }}
            </div>
            <div class="info">
                <p>Name: {{ comment.name }}</p>
                <p>Body: {{ comment.body }}</p>
            </div>
        </div>
    </div>
</template>

<script>
  import {fetchComments} from 'src/api'

  export default {
    name: 'CommentsList',
    props: {
      postId: Number
    },
    data() {
      return {
        comments: []
      }
    },
    watch: {
      async postId(newVal) {
        if (newVal) await this.fetchComments();
      }
    },
    methods: {
      async fetchComments() {
        const {data} = await fetchComments(this.postId);

        this.comments = data;
      }
    }
  }
</script>

<style lang="scss" scoped>
    .comments_list {
        .comment_element {
            box-shadow: 0 1px 20px 0 rgba(0, 0, 0, 0.1);
            padding: 15px;
            margin: 15px 0;
            display: flex;
            justify-content: space-between;
            align-items: baseline;

            .author, .info {
                display: inline-block;
            }

            .author {
                max-width: 30%;
            }

            .info {
                max-width: 70%;
            }
        }
    }
</style>
