<template>
    <el-menu class="el-menu-demo" mode="horizontal">
        <el-menu-item>
            <router-link :to="'/'">Home</router-link>
        </el-menu-item>
        <el-menu-item>
            <router-link :to="`/user/${currentUser.id}`">User</router-link>
        </el-menu-item>
        <el-menu-item>
            <a @click.prevent="logout">Logout</a>
        </el-menu-item>
    </el-menu>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
    name: 'Navigation',
    computed: {
      ...mapGetters(['currentUser'])
    },
    methods: {
      ...mapActions(['logoutUser']),
      logout() {
        this.logoutUser();

        this.$router.push('/login')
      }
    }
  }
</script>
