<template>
    <div>
        <Navigation/>
        <div>
            <h2>Post Info</h2>
            <p>Title: {{ post.title }}</p>
            <p>Body: {{ post.body }}</p>
        </div>
        <CommentsList :post-id="post.id"/>
    </div>
</template>

<script>
  import {
    Navigation,
    CommentsList
  } from 'src/components';

  import {fetchPostData} from "src/api";

  export default {
    name: 'Post',
    components: {
      Navigation,
      CommentsList
    },
    data() {
      return {
        post: {}
      }
    },
    created() {
      const id = this.$route.params.id;

      fetchPostData(id)
          .then(res => {
            const {data} = res;

            this.post = data;
          })
          .catch(err => {
            if (err.response.status === 404) {
              this.$router.push({name: 'home'})
            }
          })
    },
  }
</script>

