<template>
    <div class="posts_list">
        <div class="post_element"
             v-for="(post, index) in filteredPosts"
             :key="index">
            <h3 class="text_center">
                <router-link :to="`/posts/${post.id}`">
                    {{ post.title }}
                </router-link>
            </h3>
            <p>
                <span><strong>Body:</strong> {{ post.body }}</span>
            </p>
            <p>
                <router-link :to="`/user/${post.userId}`">
                    User Profile
                </router-link>
            </p>
        </div>
    </div>
</template>

<script>
  import {fetchPosts} from 'src/api';

  export default {
    name: 'PostsList',
    props: {
      userId: {
        type: Number
      }
    },
    data() {
      return {
        posts: []
      }
    },
    computed: {
      filteredPosts() {
        if (this.userId)
          return this.posts.filter(post => post.userId === this.userId);
        else
          return this.posts;
      }
    },
    async created() {
      await this.fetchPosts();
    },
    methods: {
      async fetchPosts() {
        const {data} = await fetchPosts();

        this.posts = data;
      }
    }
  }
</script>

<style lang="scss" scoped>
    .post_element {
        box-shadow: 0 1px 20px 0 rgba(0,0,0,.1);
        padding: 15px;
        margin: 15px 0;
    }
</style>
